<script>
    import Fa from 'svelte-fa'
    import { faXmark, faSquareMinus } from '@fortawesome/free-solid-svg-icons'

    export let display = "none";
    export let change_display;
    export let backend_url

    async function fetch_all_comments() {
        return await fetch(backend_url).then(res => res.json()).then(data => data.results)
    }

    $: comments = fetch_all_comments();

    $: {
        if (display != "none") {
            comments = fetch_all_comments();
        }
    }

    async function delete_comment(comment_id) {
        let password = prompt("비밀번호를 입력해주세요.");
        if (password == null || password == "") {
            return;
        }

        await fetch(backend_url,
            {
                method: "DELETE",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    "id": comment_id,
                    "password": password
                })
            })
        .then(res => {
            if (res.status == 200) {
                return res.json();
            } else {
                throw res.status;
            }
        })
        .then(data => {
            alert("삭제되었습니다.")
            comments = fetch_all_comments();
        })
        .catch(status => {
            if (status == "401") {
                alert("비밀번호가 틀렸습니다.\n다시 한 번 확인해주세요.");
            } else {
                console.log("Unexpected error (" + status + ")");
                alert("현재 축하글을 삭제할 수 없습니다.\n잠시 후 이용해주세요.");
            }
        });
    }
</script>

<div class="comment-outline" style="display:{display};">
    <button class="close-bnt" on:click={change_display}>
        <Fa icon={faXmark} size=.8x color="black" />
    </button>
    
    <div class="comment-list">
        <div class="header">
            방명록 축하글 전체보기
        </div>
{#await comments}
        <p>...Loading</p>
{:then comments}
        <ul class="padding-zero">
    {#each comments as comment}
            <li class="comment">
                <div class="comment-title">
                    <span class="comment-name">{comment.name}</span>
                    <button id={comment.id} on:click={() => delete_comment(comment.id)} class="delete-bnt">
                        <Fa icon={faSquareMinus} color="gray" />
                    </button>
                    <span class="comment-created">{comment.created}</span>
                </div>
                <div class="comment-content">
                    {comment.content}
                </div>
            </li>
    {/each}
        </ul>
{:catch error}
        <p>문제가 발생했습니다. 다시 시도해주세요.</p>
{/await}
    </div>
</div>

