        color: black;
        font-size: 14px;
        font-weight: 700;
        padding: 10px 0px 25px 0px;
        border-bottom: 1px solid #efefef;
        line-height: 1.2;
        margin-bottom: 30px;
    }

    .comment-outline {
        width: 100%;
        max-width: 425px;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        position: fixed;
        background: white;
        margin: auto;
        z-index: 900;
        overflow-y: scroll;
    }

    .close-bnt {
        margin: 0px;
        padding: 0px;
        background-color: transparent;
        border: 0px;
        position: absolute;
        color: #bbb;
        text-align: center;
        top: 11px;
        right: 25px;
        font-size: 26px;
        width: 30px;
        height: 30px;
        cursor: pointer
    }

    .comment-list {
        padding: 15px 25px 15px 25px;
    }

    .comment {
        margin-bottom: 20px;
        border-bottom: 1px solid #fff;
        display: block;
    }

    .comment-title {
        text-align: left;
        padding-bottom: 10px;
    }

    .comment-name {
        letter-spacing: -.5px;
        font-weight: 700;
        font-size: 12px;
    }

    .comment-created {
        float: right;
        vertical-align: initial;
        color: #555;
        letter-spacing: -.5px;
        font-size: 11px;
        margin-right: 5px;
        line-height: 18px;

    }

    .comment-content {
        text-align: left;
        font-size: 12px;
        padding: 10px 0px 15px 0px;
        border-top: 1px solid #eee;
        white-space: pre-line;
        line-height: 1.5;
    }

    .delete-bnt {
        float: right;
        margin: 0px;
        padding: 0px;
        background-color: transparent;
        border: 0px;
        cursor: pointer;
    }

