<script>
    import Fa from 'svelte-fa'
    import { faXmark } from '@fortawesome/free-solid-svg-icons'
    
    export let display = "none";
    export let change_display;
    export let backend_url
    export let swap_to_comment_display;

    let name = "";
    let password = "";
    let content = "";

    async function create_comment() {
        if (name == "") {
            alert("이름을 입력해주세요.");
            return;
        } else if (password == "") {
            alert("비밀번호를 입력해주세요.");
            return;
        } else if (content == "") {
            alert("내용을 입력해주세요.");
            return;
        }

        await fetch(backend_url,
            {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    "name": name,
                    "password": password,
                    "content": content
                })
            }
        )
        .then(res => {
            if (res.status == 200) {
                return res.json();
            } else {
                throw res.status;
            }
        })
        .then(data => {
            name = "";
            password = "";
            content = "";
            swap_to_comment_display();
            alert("축하글이 작성되었습니다!");
        })
        .catch(error => {
            console.log("Unexpected error (" + error + ")");
            alert("현재 축하글을 작성할 수 없습니다.\n잠시 후 이용해주세요.");
        });
    }
</script>

<div class="writing-outline" style="display:{display};">
    <button class="close-bnt" on:click={change_display}>
        <Fa icon={faXmark} size=0.8x color="black" />
    </button>
    <div class="writing-main">
        <div class="header">
            방명록 축하글 작성
        </div>
        <div class="row">
            <span class="column-title">
                <span class="column-text">
                    이름<b style="color:red; margin-left:3px;">*</b>
                </span>
            </span>
            <input type="text" bind:value={name} placeholder="이름을 입력해주세요." name="name" maxlength="30" autocomplete="off">
        </div>
        <div class="row">
            <span class="column-title">
                <span class="column-text">
                    비밀번호<b style="color:red; margin-left:3px;">*</b>
                </span>
            </span>
            <input type="password" bind:value={password} placeholder="비밀번호를 입력해주세요." name="password" maxlength="30" autocomplete="off">
        </div>
        <div class="row">
            <span class="column-title">
                <span class="column-text">
                    내용<b style="color:red; margin-left:3px;">*</b>
                </span>
            </span>
            <textarea type="text" bind:value={content} placeholder="내용을 입력해주세요. (최대 100자)" name="content" maxlength="100" autocomplete="off"></textarea>
        </div>
        <div style="margin-top:40px;">
            <button class="create-writing" on:click={create_comment}>
                신랑 & 신부에게 <b>축하 글 전달하기</b>
            </button>
        </div>
    </div>
</div>

