<script>
    function range(start, end) {
        let array = [];
        for (let i=start; i<end; ++i) {
            array.push(i);
        }
        return array;
    }

    let weeks = [
        ['', '', '', '', '', '', 1],
        range(2, 9),
        range(9, 16),
        range(16, 23),
        range(23, 30),
        [30]
    ]
</script>

<div class="calanderOutline">
    <div class="calander">
        <div class="month">
            4월
        </div>
        <table class="weeks">
            <thead>
                <tr>
                    <th>SUN</th>
                    <th>MON</th>
                    <th>TUE</th>
                    <th>WED</th>
                    <th>THU</th>
                    <th>FRI</th>
                    <th>SAT</th>
                </tr>
            </thead>
            <tbody>
{#each weeks as week}
                <tr>
    {#each week as date, i}
        {#if date == 22}
                    <td id=d_day>{date}</td>
        {:else if i % 7 == 0}
                    <td class=sunday>{date}</td>
        {:else if i % 7 == 6}
                    <td class=saturday>{date}</td>
        {:else}
                    <td>{date}</td>
        {/if}
    {/each}
                </tr>
{/each}
            </tbody>
        </table>
    </div>
</div>

<style>
    .calanderOutline {
        width: 425px;
        padding-bottom: 150px;
        border-bottom: 25px;
        text-align: center;
    }

    .calander {
        margin: auto;
        width: 280px;
    }

    .month {
        font-size: 19px;
        font-weight: 700;
        margin-bottom: 24px;
    }

    .weeks {
        margin: auto;
        width: 100%;
        margin-bottom: 20px;
        font-size: 12px;
        line-height: 2;
    }

    .sunday {
        color: #f79e9e;
    }

    .saturday {
        color: #78c0e9;
    }

    #d_day {
        font-size: 13px;
        background: #f79e9e;
        color: #fff;
        border-radius: 50%;
        width: 30px;
        height: 30px;
        line-height: 30px;
    }
</style>