<script>
    import Fa from 'svelte-fa'
    import { faVolumeOff, faVolumeUp } from '@fortawesome/free-solid-svg-icons'

    let bgm
    let soundOn
    if (mobileCheck()) {
        soundOn = true;
    } else {
        soundOn = false;
    }

    function handleSound() {
        if (bgm.paused) {
            bgm.play()
            soundOn = true
        } else {
            bgm.pause()
            soundOn = false
        }
    }
</script>

<div class="bgmbar">
    <button class="sound" on:click={handleSound}>
{#if soundOn}
        <Fa icon={faVolumeUp} />
{:else}
        <Fa icon={faVolumeOff} />
{/if}
    </button>
</div>
<audio id="bgm" bind:this={bgm} autoplay loop>
    <source src="https://docs.google.com/uc?export=download&id=1uDQhYAFMPthY9w7o_zYbLIOn13tAfDv8" type="audio/mp3">
</audio>


<style>
    .bgmbar {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
		margin: auto;
		width: 425px;
        text-align: center;
		height: 39px;
        z-index: 100;
    }

    .sound {
        position: absolute;
        top: 0;
