<script>
    export let bank_name, account_number, holder, display;
    export let hide;
    
    const clipboard = new ClipboardJS(".copy-btn");
    clipboard.on('success', function (e) {
        alert("복사가 완료되었습니다.");
      });

      clipboard.on('error', function (e) {
        console.log(e);
        alert("복사가 실패했습니다. 다시 시도해주세요.");
      });
</script>

<div class="account-outline" style="display:{display};">
    <div class="account-box">
        <div style="border-bottom: 1px solid #333; padding-bottom: 5px;"></div>
        <div class="account-info">
            {bank_name}
            <br>
            {account_number}
            <br>
            {holder}
        </div>
        <div class="account-btn">
            <button class="copy-btn" data-clipboard-text={account_number}>
                복사하기
            </button>
            <button on:click={hide} class="close-btn">
                닫기
            </button>
        </div>
    </div>
</div>

