<script>
    import Fa from 'svelte-fa'
    import { faPencil } from '@fortawesome/free-solid-svg-icons'
    import Writing from "./writing.svelte"
    import Comment from "./comment.svelte"

    const backend_url = "https://api.leechohyuntaeryong.com/comments";

    let writing_display = "none";
    let comment_display = "none";
    let body_overflow_y = "visible";
    function change_writing_display() {
        if (writing_display == "none") {
            writing_display = "block";
            document.body.style["overflow-y"] = "hidden";
        } else {
            writing_display = "none";
            document.body.style["overflow-y"] = "visible";
        }
    }
    function change_comment_display() {
        if (comment_display == "none") {
            comment_display = "block";
            document.body.style["overflow-y"] = "hidden";
        } else {
            comment_display = "none";
            document.body.style["overflow-y"] = "visible";
        }
    }
    function swap_to_comment_display() {
        writing_display = "none";
        comment_display = "block";
    }
</script>

<Writing display={writing_display} change_display={change_writing_display} backend_url={backend_url} swap_to_comment_display={swap_to_comment_display} />
<Comment display={comment_display} change_display={change_comment_display} backend_url={backend_url} />

<div class="guestbook-outline">
    <div class="title">방명록</div>
    <div class="guestbook-main">
        <div style="line-height:2; padding-bottom: 25px;">
            <!-- <Fa icon={faPencil} color="#f79e9e" style="margin-bottom: 10px;" /> -->
            저희 두 사람에게
            <br>
            따뜻한 한 마디를 남겨주세요.
        </div>
        <div>
            <button class="writing-guestbook" on:click={() => change_writing_display(writing_display)}>
                축하글 작성하기
            </button>
            <button class="comment-guestbook" on:click={() => change_comment_display(comment_display)}>
                방명록 전체보기
            </button>
        </div>
    </div>
</div>

